
.food{
		position: fixed;
		left: 0;
		top: 0;
		bottom: 48px;
		z-index: 30;
		width: 100%;
		background: #fff;
}
.flag-enter-active,
	.flag-leave-active{
		transition: all 0.2s linear;
		-webkit-transform: translate3d(0,0,0);
		        transform: translate3d(0,0,0);
}
.flag-enter,
	.flag-leave-to{
		-webkit-transform: translate3d(100%,0,0);
		        transform: translate3d(100%,0,0);
}
.image-header{
		position: relative;
		width: 100%;
		height: 0;
		padding-top: 100%;
}
.image-header img{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		/*height: 100%;*/
}
.image-header .back{
		position: absolute;
		top: 10px;
		left: 0;
}
.image-header .back .icon-arrow_lift{
		display: block;
		padding: 10px;
		font-size: 20px;
		color: #fff;
}
.content{
		padding: 18px;
		position: relative;
}
.content .title{
		line-height: 14px;
		margin-bottom: 8px;
		font-size: 14px;
		font-weight: 700;
		color: rgb(7,17,27);
}
.content .detail{
		margin-bottom: 18px;
		line-height: 10px;
		font-size: 0
}
.content .detail .sell-count,
	.content .detail .rating{
		font-size: 14px;
		color: rgb(147,153,159);
}
.content .detail .sell-count{
		margin-right: 12px;
}
.content .price {
		font-weight: 700;
		line-height: 24px;
}
.content .price .now{
		margin-right: 8px;
		font-size: 14px;
		color: rgb(240,20,20);
}
.content .price .old{
		text-decoration: line-through;
		font-size: 10px;
		color: rgb(147,153,159);
}
.cartcontrol-wrapper{
		position: absolute;
		right: 12px;
		bottom: 12px;
}
.buy{
		position: absolute;
		right: 18px;
		bottom: 18px;
		z-index: 10;
		height: 24px;
		line-height: 24px;
		padding: 0 12px;
		box-sizing: border-box;
		font-size: 10px;
		border-radius: 12px;
		color: #fff;
		background: rgb(0,160,220);
}
.buy-enter-active,
	.buy-leave-active{
		transition: all .2s;
		opacity: 1;
}
.buy-enter,
	.buy-leave-to{
		opacity: 0;
}
.food-content .info{
		padding: 18px;
}
.food-content .info .title{
		line-height: 14px;
		margin-bottom: 6px;
		font-size: 14px;
		color: rgb(7,17,27);
}
.food-content .info .text{
		color: rgb(77,85,93);
		line-height: 24px;
		font-size: 12px;
		padding: 0 8px;
}
.food-content .rating{
		padding: 18px;
}
.food-content .rating .title{
		line-height: 14px;
		margin-bottom: 6px;
		font-size: 14px;
		color: rgb(7,17,27);
}
.food-content .rating .rating-wrapper{
		padding: 0 18px;
}
.food-content .rating .rating-wrapper .rating-item{
		position: relative;
		padding: 16px 0;
		border-bottom:1px solid rgba(7,17,27,.1);
}
.food-content .rating .rating-wrapper .rating-item .user{
		position: absolute;
		right: 0;
		top: 16px;
		line-height: 12px;
		font-size: 0;
}
.food-content .rating .rating-wrapper .rating-item .user .name{
		display: inline-block;
		vertical-align: top;
		font-size: 10px;
		color: rgb(147,153,159);
		margin-right: 6px;
}
.food-content .rating .rating-wrapper .rating-item .user .avatar{
		border-radius: 50%;
}
.food-content .rating .rating-wrapper .rating-item .time{
		margin-bottom: 6px;
		line-height: 12px;
		font-size: 10px;
		color: rgb(147,153,159);
}
.food-content .rating .rating-wrapper .rating-item .text{
		line-height: 16px;
		font-size: 12px;
		color: rgb(7,17,27);
}
.icon-thumb_up,
	.icon-thumb_down
	{
		margin-right: 4px;
		line-height: 16px;
		font-size: 12px;
}
.icon-thumb_down{
		color: rgb(147,153,159);
}
.icon-thumb_up{
		color: rgb(0,160,220);
}
.no-rating {
		padding: 16px 0;
		font-size: 12px;
		color: rgb(147,153,159);
}



