
.ratings{
	position: absolute;
	top: 174px ;
	bottom: 0;
	width: 100%;
	left: 0;
	overflow: hidden;
}
.ratings .overview{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	padding:18px 0;
}
.ratings .overview-left{
	-webkit-box-flex:  0;
	    -ms-flex:  0 0 137px;
	        flex:  0 0 137px;
	width: 137px;
	border-right: 1px solid rgba(7,17,27,.1);
	text-align: center;
}
.overview-left .score{
	line-height: 28px;
	font-size: 24px;
	color : rgb(255,153,0);
	margin-bottom: 6px;
}
.overview-left .title {
	margin-bottom: 8px;
	line-height: 12px ;
	font-size: 12px;
	color: rgb(7,17,27);
}
.overview-left .rank{
	font-size: 10px;
	color: rgb(147,153,159);
	line-height: 10px;
}
.overview-right{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	padding: 6px 0 6px 24px;
}
@media only screen and (max-width: 320px){
.overview-right{
		padding-left: 6px;
}
.overview-left{
		-webkit-box-flex: 0;
		    -ms-flex: 0 0 120px;
		        flex: 0 0 120px;
		width: 120px;
}
}
.overview-right .score-wrapper{
	margin-bottom: 8px ;
	line-height: 18px;
	font-size: 0;
}
.overview-right .score-wrapper .title{
	display: inline-block;
	line-height: 18px;
	vertical-align: top;
	font-size: 12px;
	color: rgb(7,17,27);
}
.overview-right .score-wrapper .star{
	display: inline-block;
	vertical-align: top;
	margin:0 12px;
}
.overview-right .score-wrapper .score{
	display: inline-block;
	vertical-align: top;
	line-height: 18px;
	font-size: 12px;
	color: rgb(255,153,0);
}
.overview-right .delivery-wrapper{
	font-size: 0;
}
.overview-right .delivery-wrapper .title{
	
	font-size: 12px;
	color: rgb(7,17,27);
	line-height: 18px;
}
.overview-right .delivery-wrapper .delivery{
	font-size: 12px;
	margin-left: 12px;
	color: rgb(147,153,159);
}
.rating-wrapper{
	padding:0 18px;
}
.rating-wrapper .rating-item{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	padding: 18px 0;
	border-bottom: 1px solid rgba(7,17,27,.1);
}
.rating-wrapper .rating-item .avatar{
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 28px;
	        flex: 0 0 28px;
	width: 28px;
	margin-right: 12px;
}
.rating-wrapper .rating-item .avatar img{
	border-radius: 50%;
}
.rating-wrapper .rating-item .content{
	position: relative;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1
}
.rating-wrapper .rating-item .content .name{
	line-height: 12px;
	font-size: 10px;
	color: rgba(7,17,27);
	margin-bottom: 4px;
}
.rating-wrapper .rating-item .content .star-wrapper{
	margin-bottom: 6px;
	font-size: 0;
}
.rating-wrapper .rating-item .content .star-wrapper .star{
	display: inline-block;
	margin-right: 6px;
	vertical-align: top;
	font-size: 10px;
}
.rating-wrapper .rating-item .content .star-wrapper .delivery{
	display: inline-block;
	vertical-align: top;
	line-height: 12px;
	font-size: 10px;
	color: rgb(147,153,159);
}
.rating-wrapper .rating-item .content .text{
	line-height: 18px;
	color: rgb(7,17,27);
	margin-bottom: 8px;
	font-size: 12px;
}
.rating-wrapper .rating-item .content .recommend{
	line-height: 16px;
	font-size: 0;
}
.rating-wrapper .rating-item .content .recommend .icon-thumb_up,
.rating-wrapper .rating-item .content .recommend .item{
	display: inline-block;
	margin:0 8px 4px 0;
	font-size: 9px;
}
.rating-wrapper .rating-item .content .recommend .icon-thumb_up{
	color: rgb(0,160,220);
}
.rating-wrapper .rating-item .content .recommend .item{
	padding:  0 6px;
	border: 1px solid rgba(7,17,27,.1);
	border-radius: 1px;
	color: rgb(147,153,159);
	background: #fff;
}
.rating-wrapper .rating-item .content .time{
	position: absolute;
	top: 0;
	right: 0;
	line-height: 12px;
	font-size: 10px;
	color: rgb(147,153,159);
}



