
.shopcart{
	position: fixed;
	left: 0;
	bottom:0;
	z-index: 50;
	width: 100%;
	height: 48px;
	/*background: #000;*/
}
.shopcart .conent{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	background: #141d27;
}
.shopcart .conent .conent-left{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	font-size: 0;
}
.shopcart .conent .conent-right{
	-webkit-box-flex: 0;
	    -ms-flex: 0 0 105px;
	        flex: 0 0 105px;
	width: 105px;
}
.shopcart .conent .conent-left .logo-wrapper{
	display: inline-block;
	position: relative;
	top: -10px;
	margin: 0 12px;
	/*font-size: 24px;*/
	padding: 6px;
	width: 56px;
	height: 56px;
	box-sizing: border-box;
	border-radius: 50%;
	background: #141d27;
}
.shopcart .conent .conent-left .logo-wrapper .num{
	position: absolute;
	top: 0;
	right: 0;
	width: 24px;
	height: 16px;
	line-height: 16px;
	text-align: center;
	border-radius: 16px;
	font-size: 9px;
	font-weight: 700px;
	color: #fff;
	background: rgb(240,20,20);
	box-shadow: 0 4px 8px 0 rgba(0,0,0,.4)
}
.shopcart .conent .conent-left .logo-wrapper .logo{
	width: 100%;
	height: 100%;
	border-radius: 50%;
	background: #2b343c;
	text-align: center;
}
.shopcart .conent .conent-left .logo-wrapper .logo.hightlight{
	background: rgb(0,160,220);
}
.shopcart .conent .conent-left .logo-wrapper .logo.hightlight .icon-shopping_cart.hightlight{
	color: #fff;
}
.shopcart .conent .conent-left .price{
	display: inline-block;
	vertical-align: top;
	line-height: 24px;
	margin-top: 12px;
	box-sizing: border-box;
	padding-right: 12px;
	border-right: 1px solid rgba(255,255,255,.1);
	font-size: 16px;
	font-weight: 700;
	color: rgba(255,255,255,.4);
}
.shopcart .conent .conent-left .price.hightlight{
	color: #fff;
}
.shopcart .conent .conent-left .desc{
	display: inline-block;
	vertical-align: top;
	line-height: 24px;
	margin:12px 0 0 12px;
	font-size: 10px;
	color: rgba(255,255,255,.4);
}
.shopcart .conent .conent-left .logo-wrapper .logo .icon-shopping_cart{
	font-size: 24px;
	line-height: 44px;
	color: #80858a;
}
.shopcart .conent .conent-right .pay{
	height: 48px;
	line-height: 48px;
	text-align: center;
	font-size: 12px;
	color: rgba(255,255,255,.4);
	font-weight: 700;
	background: #2b333b;
}
.shopcart .conent .conent-right .pay.not-enough{
	background: #2b333b;
}
.shopcart .conent .conent-right .pay.enough{
	background: #00b34c;
	color: #fff;
}
.shopcart .conent .ball-container{
}
.shopcart  .ball-container .ball{
	position: fixed;
	left: 32px;
	bottom: 22px;
	z-index: 200;
	transition: all 0.4s cubic-bezier(.49,-0.29,0.75,0.41);
}
.shopcart  .ball-container .ball .inner{
	width: 16px;
	height: 16px;
	border-radius: 50%;
	background: rgb(0,160,220);
	transition: all 0.4s linear;
}
/*.move-enter-active,
.move-leave-active{
	opacity: 1;
	transform: translate3d(0,0,0);

}
.move-enter-active .inner,
.move-leave-active .inner{
	transition: all 0.4s linear;
	transform: rotate(0);
}*/
.shopcart .shopcart-list{
	position: absolute;
	top: 0;
	z-index: -1;
	left: 0;
	width: 100%;
	transition: all 0.5s ;
	/*transform: translate3d(0,-100%,0);*/
}
.fold-enter-active{
	transition: all 0.5s;
	/*transform: translate3d(0,-100%,0);*/
}
.fold-enter,
.fold-leave{
	-webkit-transform: translate3d(0,-100%,0);
	        transform: translate3d(0,-100%,0);
	/*transform: translate3d(0,0,0);*/
}

/*.fold-enter,			
.fold-leave-active{
	transform: translate3d(0,-100%,0);
}
.fold-enter-active,
.fold-leave{
	transform: translate3d(0,0,0);
}*/
.shopcart .list-header{
	height: 40px;
	line-height: 40px;
	padding: 0 18px;
	background: #f3f5f7;
	border-bottom: 1px solid rgba(7,17,27,.1);
}
.shopcart .list-header .title{
	float: left;
	font-size: 14px;
	color: rgb(7,17,27);
}
.shopcart .list-header .empty{
	float: right;
	font-size: 12px;
	color: rgb(0,160,220);
}
.shopcart .list-content{
	padding:0 18px;
	max-height: 217px;
	background: #fff;
	overflow:hidden;
}
.shopcart .list-content .food{
	position: relative;
	padding:12px 0;
	box-sizing: border-box;
	border-bottom: 1px solid rgba(7,17,27,.1);
}
.shopcart .list-content .food .name{
	line-height: 24px;
	font-size: 14px;
	color: rgb(7,17,27);
}
.shopcart .list-content .food .price{
	position: absolute;
	right: 90px;
	bottom: 12px;
	line-height: 24px;
	font-size: 14px;
	font-weight: 700;
	color: rgb(240,20,20);
}
.shopcart .list-content .food .cartcontrol-wrapper{
	position: absolute;
	right: 0;
	bottom: 6px;
}



