   vertical-align: top;
   margin-right:6px;
   background-size:12px 12px;
   background-repeat: no-repeat;
}
.goods .menu-wrapper .menu-item .icon.decrease{
   background-image: url(decrease_3@2x.png);
}
.goods .menu-wrapper .menu-item .icon.discount{
