
.cartcontrol{
	font-size: 0;
}
.cartcontrol .cart-decrease{
	display: inline-block;
	padding: 6px;
	transition:  all 0.4s linear
}
/*.cartcontrol .cart-decrease .inner{
	transition:  all 0.4s linear
}*/
.move-enter-active,
.move-leave-active{
	transition:  all .4s linear;
	opacity: 1;
	-webkit-transform: translate3d(0,0,0) rotate(0);
	        transform: translate3d(0,0,0) rotate(0);
}
/*.trn-enter-active,
.trn-leave-active{
	transition: all 0.4s linear;
	transform: rotate(0);
}
.trn-enter,
.trn-leave{
	transform: rotate(180deg);
}*/
.move-enter,
.move-leave{
	/*transition:  all 1s linear;*/
	opacity: 0;
	-webkit-transform: translate3d(24px,0,0) rotate(180deg);
	        transform: translate3d(24px,0,0) rotate(180deg);
}
.cartcontrol .cart-count{
	display: inline-block;
}
.cartcontrol .cart-add{
	display: inline-block;
	font-size: 24px;
	line-height: 24px;
	color: rgb(0,160,220);
	padding: 6px;
}
.cartcontrol .cart-count{
	font-size: 10px;
	width: 12px;
	padding-top: 6px;
	line-height: 24px;
	text-align: center;
	color: rgb(147,153,159);
	vertical-align: top;
}
.cartcontrol .inner{
	font-size: 24px;
	line-height: 24px;
	color: rgb(0,160,220);
}



